* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background: #000;
  color: #fff;
}

.fs16 {
  font-size: 16px;
}

.color01 {
  color: #ff4646;
}

progress[value]::-webkit-progress-bar {
  background-color: #fff;
  -webkit-box-shadow: 0 2px 5px rgba(130, 130, 130, 0.25) inset;
          box-shadow: 0 2px 5px rgba(130, 130, 130, 0.25) inset;
}

progress[value]::-webkit-progress-value {
  background-color: #92b0d8;
}

progress[value] {
  height: 30px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

details {
  background-color: antiquewhite;
}

details summary::marker {
  content: '';
}

details summary:after {
  content: '';
  float: left;
  width: 20px;
  height: 20px;
  background-color: #ff4646;
  display: inline-block;
  position: relative;
}

details[open] {
  background-color: aquamarine;
}

picture {
  display: block;
  max-width: 500px;
  padding: 20px;
}

picture img {
  display: inline-block;
  width: 100%;
  border-radius: 10px;
}

input[type="week"] {
  padding: 10px;
  border-radius: 0px;
}
